import * as React from 'react';
import { scroller } from 'react-scroll';
import Box from '@mui/material/Box';
import Link from '@mui/material/Link';
import Logo from '../images/logo.svg';

export default function ({ isLoaded }: { isLoaded: boolean }) {
  return (
    <Box
      component='header'
      display='flex'
      position='relative'
      width='100vw'
      height='100svh'
      justifyContent='center'
      alignItems='flex-end'
    >
      <Link
        href='#about'
        onClick={e => {
          e.preventDefault();
          scroller.scrollTo('about', {
            duration: 500,
            smooth: true
          });
        }}
        sx={{
          position: 'absolute',
          bottom: '5vw',
          backgroundImage: `url(${Logo})`,
          backgroundRepeat: 'no-repeat',
          backgroundPosition: 'center bottom',
          backgroundSize: 'contain',
          width: {
            xs: '50vw',
            md: '25vw'
          },
          height: {
            xs: '25vw',
            md: '12.5vw'
          },
          transition: 'transform 100ms ease-out',
          transform: `scale(${isLoaded ? 1.0 : 0})`,
          '&:hover': {
            cursor: 'pointer',
            md: {
              transition: 'transform 100ms ease-out',
              transform: 'scale(1.5)'
            }
          }
        }}
      />
    </Box>
  );
}
